import { useEffect, useState } from "react";
import Post from "./Post";
import { Link } from "react-router-dom";

const Posts = () => {
  const [posts, setPosts] = useState(null);
  const [loading, setLoading] = useState(true);
  const fetchPosts = async () => {
    try {
      const data = await fetch("https://jsonplaceholder.typicode.com/posts");
      const res = await data.json();
      console.log(res);
      setPosts(res);
      setLoading(false);
    } catch (error) {
      console.log(error.message);
      setLoading(true);
    }
  };
  useEffect(() => {
    setTimeout(() => {
      fetchPosts();
    }, 1000);
  }, []);
  return (
    <div className=" col-12 m-0 p-5" style={{backgroundColor:"#F9EFDB"}}>
      {loading && (
        <div className="text-center">
          <span className="spinner spinner-border border-sm"></span>
        </div>
      )}

      <h1 className="text-capitalize text-center">this is posts</h1>
     <Link className="btn btn-info btn-lg" to="/posts/create">create</Link>
      <div className="d-flex flex-wrap justify-content-center ">
        {posts &&
          posts.map((elem) => {
            return <Post posts={elem} key={elem.id} />;
          })}
      </div>
    </div>
  );
};

export default Posts;
