import { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";

const Show = () => {
  const { id } = useParams();
  const [user, setUser] = useState(null);
  const navigate = useNavigate();
  const goBack = () => {
    navigate("/users");
  };
  const fetchUser = async () => {
    try {
      const data = await fetch(
        `https://jsonplaceholder.typicode.com/users/${id}`
      );
      const res = await data.json();
      // const userItem=res.find((elem)=>elem.username===id);
      setUser(res);
    } catch (error) {
      console.log(error.message);
    }
  };
  useEffect(() => {
    fetchUser();
  }, []);
  console.log(id);
  return (
    user && (
      <div className="card mx-2 mt-2" style={{ width: "18rem" }}>
        <div className="card-body">
          <h5 className="card-title">{user.name}</h5>
          <h5 className="card-title">{user.username}</h5>
          <p className="card-text">{user.email}</p>
          <p className="card-text">street:{user.address.street}</p>
          <p className="card-text">geo.lat:{user.address.geo.lat}</p>
          <Link
            to={"/users"}
            className="btn btn-primary btn-sm text-capitalize"
          >
            Go back
          </Link>
          <button className="btn btn-danger btn-sm mx-2" onClick={goBack}>
            go back
          </button>
        </div>
      </div>
    )
  );
};

export default Show;
