import React from 'react';
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <footer className="text-white p-4" style={{backgroundColor:"#638889"}}>
      <div className="container">
        <div className="row">
          {/* Links Section */}
          <div className="col-md-4">
            <h5>Links</h5>
            <ul className="list-unstyled">
              <li><Link to="/" className='link' style={{ textDecoration: 'none',color:"#2b2b2b",fontWeight:"bold" }}>Home</Link></li>
              <li><Link to="/users" style={{ textDecoration: 'none',color:"#2b2b2b",fontWeight:"bold" }}>Users</Link></li>
              <li><Link to="/posts" style={{ textDecoration: 'none',color:"#2b2b2b",fontWeight:"bold" }}>Posts</Link></li>
            </ul>
          </div>

          {/* Contact Section */}
          <div className="col-md-4">
            <h5>Contact Us</h5>
            <address>
              123 Main Street<br />
              City, Country<br />
              Email: info@example.com<br />
              Phone: +1 (123) 456-7890
            </address>
          </div>

          {/* About Us Section */}
          <div className="col-md-4">
            <h5>About Us</h5>
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed ac quam in ex tincidunt posuere.</p>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
