import { NavLink } from "react-router-dom";

const Header = () => {
  return (  
    <nav className="navbar navbar-expand-sm bg-dark navbar-dark text-center">
      <div className="container-fluid">

        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavbar">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse justify-content-center" id="collapsibleNavbar">
          <ul className="navbar-nav">
            <li className="nav-item mx-2">
              <NavLink className={(navDate) => navDate.isActive ? "nav-link bg-success text-white text-capitalize" : "nav-link text-capitalize"} to={"/"}>home</NavLink>
            </li>
            <li className="nav-item mx-2">
              <NavLink className={(navDate) => navDate.isActive ? "nav-link bg-success text-white text-capitalize" : "nav-link text-capitalize"} to={"/users"}>users</NavLink>
            </li>
            <li className="nav-item mx-2">
              <NavLink className={(navDate) => navDate.isActive ? "nav-link bg-success text-white text-capitalize" : "nav-link text-capitalize"} to={"/posts"}>posts</NavLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default Header;
