import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import Swal from "sweetalert2";

const ShowPosts = () => {
        const {id}=useParams();
        const [post,setPost]=useState(null)
        const navigate=useNavigate();
        const fetchPost=async()=>{
                try {
                        const data=await fetch(`https://jsonplaceholder.typicode.com/posts/${id}`);
                        const res=await data.json();
                        console.log(res);
                        setPost(res);     
                } catch (error) {
                 console.log(error.message);       
                }
        }
        const deletePost=async()=>{
          const res=await axios.delete(`https://jsonplaceholder.typicode.com/posts/${id}`, {
  method: 'DELETE',
  
}
);
console.log(res.data);

Swal.fire({
        title: "Are you sure baby?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!"
        
      }
      ).then((result) => {
        if (result.isConfirmed) {
          Swal.fire({
            title: "Deleted!",
            text: "Your file has been deleted.",
            icon: "success"
          });
          navigate("/posts")
        }
      });
      
        }
        const editPost=()=>{
          navigate(`/posts/edit/${id}`)
        }
       
        useEffect(()=>{
                fetchPost();
                
        },[])
        return ( 
                post &&  
                <div style={{ marginLeft: 'auto', marginRight: 'auto', width: '18rem',marginTop:"10px" }}>
                <h5>{post.id}</h5>
                <div className="card" style={{width:"18rem"}}>
 
  <div className="card-body">
    <h5 className="card-title" style={{color:"blue",textTransform:"capitalize"}}>{post.title}</h5>
    <p className="card-text">{post.body}</p>
    <Link to={"/posts"} className="btn btn-success btn-sm">go back</Link>
    <button className="btn btn-danger text-capitalize btn-sm ms-2" onClick={deletePost}>delete</button>
   <button className="btn btn-warning btn-sm mx-2" onClick={editPost}>edit</button>
   
  </div>
</div>
</div>
         );
}
 
export default ShowPosts;