import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";

const Update = ({posts}) => {
        const [title, setTitle] = useState("");
        const [body, setBody] = useState("");
        console.log(posts);
        useEffect(()=>{
                setTitle(posts.title);
                setBody(posts.body)
        },[])
        const navigate=useNavigate();
      
      const sendSubmit=(e)=>{
        e.preventDefault()
       const update=async()=>{
        try {
              const res=await axios.put(`https://jsonplaceholder.typicode.com/posts/${posts.id}`,{
                title:title,
                body:body
              },{method:"put",
              headers:{
                'Content-Type': 'application/json;charset=utf-8'

              }},
             )
              console.log(res.data);  
        } catch (error) {
          console.log(error.message);      
        }
       }
       update();
       Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success"
      });
      navigate("/posts")
      }
      
        return ( 
                <div className="bg-dark col-6 offset-3 mt-5 text-white p-5">
                <form action="#" method="post" onSubmit={(e) => sendSubmit(e)}>
                  <div className="d-block my-2">
                    <label htmlFor="title" className="text-capitalize mb-2">
                      title:
                    </label>
                    <input
                      type="text"
                      name="title"
                      id="title"
                      className="form-control"
                      placeholder="title..."
                      onChange={(e) => setTitle(e.target.value)}
                      value={title}
                    />
                   
                  </div>
        
                  <div className="d-block my-2">
                    <label htmlFor="body" className="text-capitalize mb-2">
                      body:
                    </label>
                    <textarea
                      id="body"
                      className="form-control"
                      placeholder="body..."
                      style={{ height: "200px", resize: "none" }}
                      onChange={(e) => setBody(e.target.value)}
                      value={body}
                    />
                      <button
                      type="submit"
                      className="btn btn-danger mt-2 text-capitalize"
                      disabled={!(title.length > 5 && body.length > 5)}
                    >
                      edit
                    </button>
                  </div>
                </form>
              </div>
         );
}
 
export default Update;