import { Link } from "react-router-dom";

const User = ({users}) => {
        return (
                users && 
                <div className="card mx-2 mt-2" style={{width: "18rem"}}>
                <div className="card-body">
                  <h5 className="card-title"><Link to={`/users/${users.id}`}>{users.name}</Link></h5>
                  <h5 className="card-title">{users.username}</h5>
                  <p className="card-text">{users.email}</p>
                  <p className="card-text">street:{users.address.street}</p>
                  <p className="card-text">geo.lat:{users.address.geo.lat}</p>
                </div>
              </div>

          );
}
 
export default User;