import axios from "axios";
import { useState , useRef } from "react";
import { useNavigate } from "react-router-dom";
import Swal from 'sweetalert2'
const Create = () => {
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const titleRef=useRef("");
const bodyRef=useRef("");
const navigate=useNavigate();
  const createPost = async () => {
    try {
      const response = await axios.post('https://jsonplaceholder.typicode.com/posts', {
        title: title,
        body: body
      },{headers:{'Content-type': 'application/json; charset=UTF-8'}});
      console.log(response.data);
      Swal.fire({
        title: "Good job!",
        text: "You clicked the button!",
        icon: "success"
      });

      titleRef.current.value="";
      bodyRef.current.value="";
      navigate("/posts")
       

    } catch (error) {
      console.log(error.message);
    }
  };

  const sendSubmit = (e) => {
    e.preventDefault();
   
  };


  return (
    <div className="bg-dark col-6 offset-3 mt-5 text-white p-5">
      <form action="#" method="post" onSubmit={(e) => sendSubmit(e)}> 
        <div className="d-block my-2">
          <label htmlFor="title" className="text-capitalize mb-2">title:</label>
          <input
            type="text"
            name="title"
            id="title"
            className="form-control"
            placeholder="title..."
            onChange={(e) => setTitle(e.target.value)}
            ref={titleRef}
          />
          {title.length === 0 ? <p className="text-danger">This field is required</p> : ""}
          {title.length >= 1 && title.length <= 5 ? <p class="text-danger">This field should be more than 5 characters</p> : ""}
          {title.length > 500 ? <p className="text-danger">This field should be less than 500 characters</p> : ""}
        </div>
        
        <div className="d-block my-2">
          <label htmlFor="body" className="text-capitalize mb-2">body:</label>
          <textarea
            id="body"
            className="form-control"
            placeholder="body..."
            style={{ height: "200px", resize: "none" }}
            onChange={(e) => setBody(e.target.value)}
            ref={bodyRef}
          />
          {body.length === 0 ? <p className="text-danger">This field is required</p> : ""}
          {body.length >= 1 && body.length <= 5 ? <p className="text-danger">This field should be more than 5 characters</p> : ""}
          {body.length > 500 ? <p className="text-danger">This field should be less than 500 characters</p> : ""}
          <button type="submit" className="btn btn-danger mt-2" onClick={createPost} disabled={!(title.length > 5 && body.length > 5)}>
            Create
          </button>
          </div>
      </form>
    </div>
  );
};

export default Create;
