import { Link } from "react-router-dom";

const Post = ({posts}) => {
        return (
                posts &&  
                <div className="mx-2 my-2">
                <h5>{posts.id}</h5>
                <div className="card" style={{width:"18rem"}}>
 
  <div className="card-body">
    <h5 className="card-title" style={{color:"blue",textTransform:"capitalize"}}><Link to={`/posts/${posts.id}`}>{posts.title.substr(0,10)}</Link></h5>
    <p className="card-text">{posts.body.substr(0,50)}...</p>
   
  </div>
</div>
</div>
         );
}
 
export default Post;